body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.circle-btn {
  background-color: #f5f5f5;
  width: 1.5em;
  height: 1.5em;
  border-radius: 5em;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bold;
  font-size: 1em;
  color: #353744; }
  .circle-btn > * {
    -webkit-transition-duration: 300ms;
            transition-duration: 300ms;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform; }
  .circle-btn:hover > * {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  .circle-btn:active {
    opacity: 50%; }
  .circle-btn .text {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start; }
  .circle-btn > * {
    width: 15px;
    height: 15px; }
    .circle-btn > * path {
      fill: #353744; }

.logo-container {
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .logo-container .logo-text {
    color: #50d19e;
    font-size: 2em;
    font-style: italic;
    font-weight: bold;
    margin-right: 0.2em; }

@media (max-width: 679px) {
  .navbar-container {
    grid-template-columns: minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em);
    grid-template-rows: 1fr 1fr;
    grid-template-areas: ". logo logo sitenav ." "roomnav roomnav roomnav roomnav roomnav"; } }

@media (min-width: 680px) {
  .navbar-container {
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-areas: "logo roomnav sitenav"; } }

.navbar-container {
  background-color: #353744;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  width: 100%;
  padding: 0 1em;
  box-sizing: border-box;
  display: grid; }
  .navbar-container .logo-section {
    grid-area: logo; }
  .navbar-container .room-nav {
    grid-area: roomnav;
    display: flex;
    justify-content: center;
    align-items: center; }
    .navbar-container .room-nav .circle-hover:hover {
      background-color: #cacaca; }
    .navbar-container .room-nav .room-nav-btn {
      margin: 0 0.15em; }
    .navbar-container .room-nav .home-btn,
    .navbar-container .room-nav .search-btn,
    .navbar-container .room-nav .create-room-btn {
      background-color: transparent; }
      .navbar-container .room-nav .home-btn svg path,
      .navbar-container .room-nav .search-btn svg path,
      .navbar-container .room-nav .create-room-btn svg path {
        fill: #f6f6f6; }
      .navbar-container .room-nav .home-btn:hover,
      .navbar-container .room-nav .search-btn:hover,
      .navbar-container .room-nav .create-room-btn:hover {
        background-color: #cacaca; }
        .navbar-container .room-nav .home-btn:hover svg path,
        .navbar-container .room-nav .search-btn:hover svg path,
        .navbar-container .room-nav .create-room-btn:hover svg path {
          fill: #353744; }
    .navbar-container .room-nav .search-btn svg {
      -webkit-transform: none;
              transform: none; }
  .navbar-container .site-nav {
    grid-area: sitenav;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
    .navbar-container .site-nav .more-pages-btn {
      margin: 0 0.15em;
      background-color: #50d19e; }
    .navbar-container .site-nav .user-settings-btn {
      margin: 0 0.15em;
      background-color: #ffe96f; }
    .navbar-container .site-nav .logout-btn {
      margin: 0 0.15em;
      background-color: #e9524a; }




